import React, { Component } from 'react';

import PropTypes from 'prop-types';


import slot from '../../Common/Util/slot';

import classNames from 'classnames';

import './input.css';

export const theme = {
	CLASS_WRAPPER_NOT_EMPTY: 'rmd-input--wrapper__not_empty',
	CLASS_WRAPPER_ACTIVE: 'rmd-input--wrapper__active',
	CLASS_ACTIVE: 'rmd-input__active',
	CLASS_WRAPPER_WITHOUT_LABEL: 'rmd-input--wrapper__without_label',

	CLASSES: 'rmd-input',
	CLASSES_WRAPPER: 'rmd-input--wrapper',
	CLASSES_CONTENT: 'rmd-input--content',
	CLASSES_FOOTER: 'rmd-input--footer',

	CLASSES_WRAPPER_HAS_ERROR: 'rmd-input--wrapper__has-error',
	CLASSES_WRAPPER_HAS_WARNING: 'rmd-input--wrapper__has-warning',
};


class Input extends Component {
	constructor(props) {
		super(props);

		this.theme = theme;

		this._refs = {
			$input: undefined,
			$wrapper: undefined,
		};

		this.id = 'input-' + Math.random();

		this.isActive = false;

		this.handleInputRef = this.handleInputRef.bind(this);
		this.handleWrapperRef = this.handleWrapperRef.bind(this);

		this.handleFocus = this.handleFocus.bind(this);
		this.handleBlur = this.handleBlur.bind(this);
	}

	handleFocus(e) {

		const { _refs: refs, props, theme } = this;

		if (refs.$wrapper) {
			const $wrapper = refs.$wrapper;

			$wrapper.classList.add(theme.CLASS_WRAPPER_ACTIVE);
			this.isActive = true;
		}

		if (props.onFocus) {
			return props.onFocus(e);
		}
	};

	handleBlur(e) {

		const { _refs: refs, props, theme } = this;

		if (refs.$wrapper) {
			const $wrapper = refs.$wrapper;

			$wrapper.classList.remove(theme.CLASS_WRAPPER_ACTIVE);
			this.isActive = false;

			if (e.target.value) {
				$wrapper.classList.add(theme.CLASS_WRAPPER_NOT_EMPTY);
			} else {
				$wrapper.classList.remove(theme.CLASS_WRAPPER_NOT_EMPTY);
			}
		}

		if (props.onBlur) {
			return props.onBlur(e);
		}
	};


	handleInputRef(el) {
		this._refs.$input = el;
	}

	handleWrapperRef(el) {
		this._refs.$wrapper = el;
	}

	renderInput({ _theme, style, inputStyle, inputClass, props  }) {
		return (
			<input
				{ ...props }
				id={ this.id }
				style={ style || inputStyle }
				className={ classNames(_theme.CLASSES, props.class, props.className, inputClass) }
				ref={ this.handleInputRef }
				onFocus={ this.handleFocus }
				onBlur={ this.handleBlur }
			/>
		)
	}

	render() {

		const { style, inputStyle, wrapperStyle, inputClass, wrapperClass, children, ...props  } = this.props;

		const _theme = this.theme;

		const { label, footer } = slot(children);

		return (
			<div
				style={ style || wrapperStyle }
				className={ classNames(_theme.CLASSES_WRAPPER, wrapperClass, {
					[ _theme.CLASS_WRAPPER_ACTIVE ]: this.isActive,
					[ _theme.CLASS_WRAPPER_NOT_EMPTY ]: props.value || props.defaultValue,
					[ _theme.CLASS_WRAPPER_WITHOUT_LABEL ]: ! label,
				}) }
				ref={ this.handleWrapperRef }
			>
				<div className={ _theme.CLASSES_CONTENT }>
					{ label ?
						<label htmlFor={ this.id }>
							{ label }
						</label>
						: null
					}
					{ this.renderInput({ _theme, style, inputStyle, inputClass, props  }) }
				</div>
				{ footer ?
					<div className={ _theme.CLASSES_FOOTER }>
						{ footer }
					</div>
					: null
				}
			</div>
		);
	}
}

Input.propTypes = {
	inputStyle: PropTypes.any,
 	wrapperStyle: PropTypes.any,
 	inputClass: PropTypes.string,
 	wrapperClass: PropTypes.string,
};


export default Input;



